"use client"

import { useState } from "react"
import { createFileLog } from "@/actions/file-actions"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Label } from "@/components/ui/label"
import {
    Dialog,
    DialogContent,
    DialogDescription,
    DialogHeader,
    DialogTitle,
    DialogTrigger,
    DialogFooter
} from "@/components/ui/dialog"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"
import { Plus, Loader2 } from "lucide-react"

type ProjectOption = { id: string; name: string }

export function AddFileDialog({ projects }: { projects: ProjectOption[] }) {
    const [open, setOpen] = useState(false)
    const [pending, setPending] = useState(false)
    const [selectedProjectId, setSelectedProjectId] = useState<string>("")

    async function handleSubmit(formData: FormData) {
        setPending(true)
        const result = await createFileLog(formData)
        setPending(false)

        if (result?.success) {
            setOpen(false)
        } else {
            console.error(result?.error)
        }
    }

    return (
        <Dialog open={open} onOpenChange={setOpen}>
            <DialogTrigger asChild>
                <Button className="gap-2">
                    <Plus className="h-4 w-4" /> Add File
                </Button>
            </DialogTrigger>
            <DialogContent>
                <DialogHeader>
                    <DialogTitle>Add File Link</DialogTitle>
                    <DialogDescription>
                        Log a completed deliverable.
                    </DialogDescription>
                </DialogHeader>
                <form action={handleSubmit} className="space-y-4">
                    <div className="space-y-2">
                        <Label htmlFor="title">File Title</Label>
                        <Input id="title" name="title" placeholder="e.g. Q4 Report" required />
                    </div>

                    <div className="space-y-2">
                        <Label htmlFor="driveLink">Drive Link</Label>
                        <Input id="driveLink" name="driveLink" placeholder="https://docs.google.com/..." required />
                    </div>

                    <div className="space-y-2">
                        <Label htmlFor="projectId">Project (Optional)</Label>
                        <Select name="projectId" onValueChange={setSelectedProjectId}>
                            <SelectTrigger>
                                <SelectValue placeholder="Select Project" />
                            </SelectTrigger>
                            <SelectContent>
                                <SelectItem value="none">None</SelectItem>
                                {projects.map((p) => (
                                    <SelectItem key={p.id} value={p.id}>{p.name}</SelectItem>
                                ))}
                            </SelectContent>
                        </Select>
                    </div>


                    <DialogFooter>
                        <Button type="submit" disabled={pending}>
                            {pending ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : "Save File"}
                        </Button>
                    </DialogFooter>
                </form>
            </DialogContent>
        </Dialog >
    )
}
